@charset "UTF-8";
@font-face {
  font-family: "Main";
  src: url("~@/src/assets/Futura LT Heavy.otf"); }

html,
body {
  font-size: calc(100vw/375*100);
  height: 100%; }

@media screen and (min-width: 600px) {
  html {
    font-size: 160px; } }

@media screen and (min-width: 750px) {
  html {
    font-size: 195px; } }

#root {
  min-height: 100%;
  position: relative;
  overflow: hidden;
  padding-bottom: 0.8rem;
  box-sizing: border-box; }

@keyframes fadeIn {
  from {
    opacity: 0; }
  to {
    opacity: 1; } }

@keyframes slideUp {
  from {
    transform: translateY(20px);
    opacity: 0; }
  to {
    opacity: 1; } }

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
main,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0); }

/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
main,
menu,
nav,
section {
  display: block; }

body {
  line-height: 1; }

ol,
ul {
  list-style: none; }

blockquote,
q {
  quotes: none; }

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none; }

/* Don't kill focus outline for keyboard users: http://24ways.org/2009/dont-lose-your-focus */
a:hover,
a:active {
  outline: none; }

table {
  border-collapse: collapse;
  border-spacing: 0; }

/*这里定义整个应用加载时的动画，进场前这里设置了缩放为0，
进场过程中缩放变为1，也就是100%，持续时间为500ms，
时间曲是ease-out，一种先快后慢的变化曲线*/
.appAppear-appear {
  opacity: 0;
  transform: scale(0); }

.appAppear-appear.appAppear-appear-active {
  opacity: 1;
  transform: scale(1);
  transition: 500ms ease-out; }

/*设置进场前透明度为0，放缩为0*/
.fade-enter {
  opacity: 0; }

/*设置进场过程中 透明度为1，放缩为1，放缩的位置为左上角，持续时间为500ms，时间曲是ease-out */
.fade-enter-active {
  opacity: 1;
  transition: all 500ms ease-out; }

/*设置退场前透明度为1，放缩为1*/
.fade-exit {
  opacity: 1; }

/*设置退场过程中 透明度为0，放缩为0，放缩的位置默认是中间，所以不设置也可以，
  持续时间为500ms，时间曲是ease-out */
.fade-exit-active {
  position: absolute;
  z-index: -1;
  opacity: 0;
  transition: all 0ms ease-out; }

.project-item {
  width: 3.75rem;
  max-width: 370px;
  height: auto;
  box-sizing: border-box;
  padding: 0.2rem;
  margin: 0.13rem 0.25rem;
  border: 0.01rem solid transparent;
  transition: all 0.3s;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-shadow: 0 0.05rem 0.05rem #e8eaf6, 0 -0.03rem 0.05rem rgba(232, 234, 246, 0.5);
  border-radius: 0.25rem;
  background: #fff;
  cursor: pointer;
  user-select: none; }
  .project-item:active {
    transform: scale(0.9); }
  .project-item img {
    width: 100%;
    display: block;
    background: #fff; }
  .project-item .content {
    box-sizing: border-box;
    margin-top: 0.2rem; }
    .project-item .content .title {
      font-size: 18px;
      padding-bottom: 0.1rem;
      color: #c09c00;
      font-weight: bold; }
    .project-item .content p {
      font-size: 14px;
      line-height: 1.5;
      color: #666;
      overflow: hidden;
      text-align: justify; }
    .project-item .content .more {
      display: block;
      font-size: 14px;
      line-height: 18px;
      color: #24292e;
      font-weight: bold;
      margin-top: 0.05rem; }
